import React, { useEffect } from "react";
import { Edit } from "@mui/icons-material";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { fetchAllMemberByUserIdAction } from "../../../../app/service/action/usersAction";
import defaultImage from '../../../../assets/image/logo-pbt.png'
import Loading from "../../../../components/Loading/Loading";
import TitleComponent from "../../../../components/Title/TitleComponent";

const Riders = () => {
  const navigate = useNavigate();
  const dispatch: any = useDispatch()
  const { isLoading, data } = useSelector(({ membersByUserIdState }) => membersByUserIdState.membersByUserId)
  const userDetail = JSON.parse(localStorage.getItem('userDetail'))
  const handleImageError = (event) => {
    event.target.src = defaultImage; // Mengubah src menjadi gambar placeholder atau not found
  };

  useEffect(() => {
    dispatch(fetchAllMemberByUserIdAction())
  }, [dispatch]);

  const handleAddRider = () => {
    navigate(`add-rider/${userDetail.id}`)
  }

  const handleOrderJersey = () => {
    navigate(`order-jersey/${userDetail.id}`)
  }
  return (
    <div id={'setting-rider'}>
      <div className={'button'}>
        <button className={'add-rider-button'} onClick={handleAddRider}>Tambah Rider</button>
        <button className={'order-jersey-button'} onClick={handleOrderJersey}>Pesan Jersey</button>
      </div>
      <div className={'divider'}></div>
      {isLoading ? (
        <Loading />
      ) : (
        <>
          <TitleComponent title={'Data Rider'} />
          {data.result.map((item) => {
            return (
              <div className="card-rider" key={item.id}>
                <div className="image-rider">
                  <img src={item.image_profile} alt="" onError={handleImageError} />
                </div>
                <div className="data-rider">
                  <div className="content-rider">
                    <div className="content-rider-label">Full Name
                    </div>
                    <div className="content-rider-value">: {item.name_rider}
                    </div>
                  </div>
                  <div className="content-rider">
                    <div className="content-rider-label">Nickname
                    </div>
                    <div className="content-rider-value">: {item.nickname}
                    </div>
                  </div>
                  <div className="content-rider">
                    <div className="content-rider-label">Category
                    </div>
                    <div className="content-rider-value">: {item.category_race_name}
                    </div>
                  </div>
                  <div className="content-rider-button-detail">
                    <button onClick={() => navigate(`/member/setting/riders/${item.id}`)}>
                      <Edit />
                      <p> Detail Rider</p>
                    </button>
                  </div>
                </div>
              </div>
            )
          })}
        </>
      )}

    </div>
  )
}

export default Riders
